import React from 'react';
import PropTypes from 'prop-types';
import { DragSource } from 'react-dnd';
// import { GlobalInfosContext } from '../model/react-context/GlobalInfosProvider';


let dragCardTypes = {
  DESCRIPTION: 'description'
}


const dragSource = {
  beginDrag(props) {
    return props.id;
  }
};

const collect = (connect, monitor) => {
  return {
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging()
  }
}

class DragCard extends React.Component {
  //constructor(props) {
    //super(props);
    //this.state = {};
  //}

  render() {
    const { connectDragSource, isDragging, color } = this.props;
    return(
      <div 
        className={`drag-card`} 
        style={{backgroundColor: isDragging ? 'white' : (color ? color : 'purple')}}
        >
        {this.props.children}
      </div>
    )
  }
}

DragCard.propTypes = {
  connectDragSource: PropTypes.func.isRequired,
  isDragging: PropTypes.bool.isRequired,
  color: PropTypes.string,
  id: PropTypes.number
};


export default DragSource(dragCardTypes.DESCRIPTION, dragSource, collect)(DragCard);



