import React from 'react';
import DragCard from './DragCard';
import DropCard from './DropCard';
import { DropTarget } from 'react-dnd';
import PropTypes from 'prop-types';

let dragCardTypes = {
  DESCRIPTION: 'description'
}

const dropCardTarget = {
  drop(props, monitor, movingFunctionCard) {
    const card = monitor.getItem()
    movingFunctionCard(card, props.dragStartOrEnd, props.dragPosition);
  }
};

function collect(connect, monitor) {
  return {
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver()
  };
}

class DropOrDragCardContainer extends React.Component {
  //constructor(props) {
    //super(props);
    //this.state = {};
  //}

  constructor(props) {
    super(props);
    this.state = {};

  }

  render() {
    const {dragStartOrEnd, dragPosition, connectDropTarget, isOver } = this.props;

    return connectDropTarget(
      <div className="drop-card__container">
        <DropCard className={`drop-card`}>
          {this.props.children}
        </DropCard>
      </div>
    )
  }
}

DropOrDragCardContainer.propTypes = {
  dragStartOrEnd: PropTypes.number.isRequired,
  dragPosition: PropTypes.number.isRequired,
  connectDropTarget: PropTypes.func.isRequired,
  isOver: PropTypes.bool.isRequired
};

export default DropTarget(dragCardTypes.DESCRIPTION, dropCardTarget, collect)(DropOrDragCardContainer);



